"use client"

import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Progress } from "@/components/ui/progress"
import GameWorld from "@/components/game/GameWorld"
import { GameProvider, useGame } from "@/contexts/GameContext"

function GameContent() {
  const { gameState, updateEnergy, updateCoins, addExperience } = useGame()
  const { player, gameTime } = gameState

  const handleAction = (action: string) => {
    switch (action) {
      case 'farm':
        updateEnergy(-10)
        updateCoins(15)
        addExperience(5)
        break
      case 'mine':
        updateEnergy(-15)
        updateCoins(25)
        addExperience(8)
        break
      case 'fish':
        updateEnergy(-8)
        updateCoins(20)
        addExperience(6)
        break
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-b from-gray-900 to-gray-800 text-white">
      <div className="max-w-[1400px] mx-auto px-6 py-4">
        {/* Top Bar - Stats */}
      <div className="fixed top-0 left-0 right-0 bg-gray-900/95 border-b-2 border-gray-600 p-4 z-50 backdrop-blur-sm shadow-lg">
        <div className="max-w-[1400px] mx-auto grid grid-cols-12 gap-6">
            <div className="col-span-3 flex items-center space-x-4">
              <Card className="bg-gray-800/90 border-2 border-gray-600 p-3 shadow-lg flex-1">
                <p className="text-base font-bold text-white">Level {player.level}</p>
                <Progress 
                  value={(player.experience / (player.level * 100)) * 100} 
                  className="h-3 mt-2" 
                  variant="level"
                />
              </Card>
              
              <Card className="bg-gray-800/90 border-2 border-gray-600 p-3 shadow-lg flex-1">
                <p className="text-base font-bold text-white">Energy</p>
                <Progress value={player.energy} className="h-3 mt-2" variant="energy" />
              </Card>
            </div>

            <div className="col-span-6 flex justify-center">
              <Card className="bg-gray-800/90 border-2 border-gray-600 p-3 shadow-lg">
                <p className="text-base font-bold text-white text-center">
                  {gameTime.season} {gameTime.day}<br />
                  {String(gameTime.hour).padStart(2, '0')}:{String(gameTime.minute).padStart(2, '0')}
                </p>
              </Card>
            </div>

            <div className="col-span-3 flex justify-end">
              <Card className="bg-gray-800/90 border-2 border-gray-600 p-3 shadow-lg">
                <p className="text-base font-bold text-white">ðŸª™ {player.coins}</p>
              </Card>
            </div>
          </div>
        </div>

        {/* Game Content */}
        <div className="mt-28">
          <div className="grid grid-cols-12 gap-6 items-start">
            {/* Left Sidebar - Inventory */}
            <Card className="col-span-3 bg-gray-800/90 border-2 border-gray-600 p-6 shadow-lg sticky top-28 overflow-hidden">
              <h2 className="text-2xl font-bold mb-4 text-white">Inventory</h2>
              <div className="grid grid-cols-4 gap-3">
                {Array.from({ length: gameState.inventory.maxSlots }).map((_, i) => (
                  <div
                    key={i}
                    className="aspect-square bg-gray-700/80 rounded-lg border-2 border-gray-600 hover:border-gray-400 transition-colors cursor-pointer shadow-md"
                  />
                ))}
              </div>
            </Card>

            {/* Main Game Area */}
            <div className="col-span-6 bg-gray-800/90 rounded-xl border-2 border-gray-600 min-h-[600px] relative overflow-hidden shadow-2xl">
              <div className="absolute inset-0 bg-gradient-to-b from-transparent to-gray-900/20"></div>
              <GameWorld />
            </div>

            {/* Right Sidebar - Actions & Chat */}
            <Card className="col-span-3 bg-gray-800/90 border-2 border-gray-600 p-6 shadow-lg sticky top-28 overflow-hidden">
              <div className="space-y-6">
                <div>
                  <h2 className="text-2xl font-bold mb-4 text-white">Actions</h2>
                  <div className="space-y-3">
                    <Button 
                      className="w-full text-lg py-3 bg-green-600 hover:bg-green-700 text-white border-2 border-green-500" 
                      variant="outline"
                      onClick={() => handleAction('farm')}
                      disabled={player.energy < 10}
                    >
                      Farm (-10 Energy)
                    </Button>
                    <Button 
                      className="w-full text-lg py-3 bg-gray-600 hover:bg-gray-700 text-white border-2 border-gray-500" 
                      variant="outline"
                      onClick={() => handleAction('mine')}
                      disabled={player.energy < 15}
                    >
                      Mine (-15 Energy)
                    </Button>
                    <Button 
                      className="w-full text-lg py-3 bg-blue-600 hover:bg-blue-700 text-white border-2 border-blue-500" 
                      variant="outline"
                      onClick={() => handleAction('fish')}
                      disabled={player.energy < 8}
                    >
                      Fish (-8 Energy)
                    </Button>
                    <Button 
                      className="w-full text-lg py-3 bg-yellow-600 hover:bg-yellow-700 text-white border-2 border-yellow-500" 
                      variant="outline"
                    >
                      Market
                    </Button>
                  </div>
                </div>

                <div className="mt-6">
                  <h2 className="text-2xl font-bold mb-4 text-white">Chat</h2>
                  <div className="bg-gray-900/80 rounded-lg p-4 h-48 overflow-y-auto mb-3 border-2 border-gray-700">
                    <p className="text-lg text-gray-300">Welcome to Easter Valley!</p>
                  </div>
                  <div className="flex gap-3">
                    <input
                      type="text"
                      className="flex-1 bg-gray-700/80 rounded-lg px-4 py-2 text-lg text-white border-2 border-gray-600 focus:border-gray-400 focus:outline-none"
                      placeholder="Type a message..."
                    />
                    <Button 
                      className="px-6 bg-indigo-600 hover:bg-indigo-700 text-white border-2 border-indigo-500"
                      variant="secondary" 
                      size="lg"
                    >
                      Send
                    </Button>
                  </div>
                </div>
              </div>
            </Card>
          </div>
        </div>
      </div>
    </div>
  )
}

export default function EasterValley() {
  return (
    <GameProvider>
      <GameContent />
    </GameProvider>
  )
}
